<template>
  <div class="calendar">
    <a
      href="#"
      v-on:click.prevent="togglePopup();"
      class="settings__setting settings__setting-dates flex-column"
    >
      <span class="settings__label">Даты аренды</span>
      <span class="settings__choice">
        {{ from.getDate() + " " + shortMonthStr(from)
        }}<span v-if="from.getYear() != to.getYear()">{{
          " " + to.getFullYear() + " "
        }}</span>
        – {{ to.getDate() + " " + shortMonthStr(to) }} {{ to.getFullYear()
        }}<span id="date_total"> · {{ days }} {{ dayString(days) }}</span>
      </span>
    </a>
    <div id="calendar-outer">
      <div class="calendar-inner">
        <div class="months">
          <div class="month">
            <div class="header">
              <a class="arrow arrow-back" @click="movePreviousMonth"
                ><svg
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    clip-rule="evenodd"
                    d="M16 7H3.83L9.42 1.41L8 0L0 8L8 16L9.41 14.59L3.83 9H16V7Z"
                  />
                </svg>
              </a>
              <span class="title"> {{ header.label }} </span>
            </div>
            <div class="weekdays">
              <div class="weekday" v-for="weekday in weekdays">
                {{ weekday.label_3 }}
              </div>
            </div>
            <div class="week" v-for="week in weeks">
              <div
                class="day"
                :class="{
                  today: day.isToday,
                  'not-in-month': !day.inMonth,
                  selected: isSelected(day),
                  between: isBetween(day),
                  highlighted: isHighlighted(day),
                  disabled: isDisabled(day)
                }"
                v-for="day in week"
                @click="selectDay(day);"
                @mouseover="hoverDay(day);"
              >
                {{ day["label"] }}
              </div>
            </div>
          </div>
          <div class="month">
            <div class="header">
              <span class="title"> {{ header2.label }} </span>
              <a class="arrow arrow-forward" @click="moveNextMonth"
                ><svg
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    clip-rule="evenodd"
                    d="M8 0L6.59 1.41L12.17 7H0V9H12.17L6.59 14.59L8 16L16 8L8 0Z"
                  />
                </svg>
              </a>
            </div>
            <div class="weekdays">
              <div class="weekday" v-for="weekday in weekdays">
                {{ weekday.label_3 }}
              </div>
            </div>
            <div class="week" v-for="week in weeks2">
              <div
                class="day"
                :class="{
                  today: day.isToday,
                  'not-in-month': !day.inMonth,
                  selected: isSelected(day),
                  between: isBetween(day),
                  highlighted: isHighlighted(day)
                }"
                v-for="day in week"
                @click="selectDay(day);"
                @mouseover="hoverDay(day);"
              >
                {{ day["label"] }}
              </div>
            </div>
          </div>
        </div>
        <div class="calendar__buttons">
          <a
            href="#"
            v-if="$store.state.finished && $store.state.cars_count > 0"
            v-on:click.prevent="see_cars();"
            id="calendar__show-cars"
            >Посмотреть {{ $store.state.cars_count }}
            {{ automobilesString($store.state.cars_count) }}</a
          >

          <span
            v-if="$store.state.finished && $store.state.cars_count == 0"
            class="calendar__not-found"
            >Автомобили не найдены</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Calendar data
const _daysInMonths = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
const _weekdayLabels = ["пн", "вт", "ср", "чт", "пт", "сб", "вс"];
const _monthLabels = [
  "Январь",
  "Февраль",
  "Март",
  "Апрель",
  "Май",
  "Июнь",
  "Июль",
  "Август",
  "Сентябрь",
  "Октябрь",
  "Ноябрь",
  "Декабрь"
];

const _today = new Date();
const _todayComps = {
  year: _today.getFullYear(),
  month: _today.getMonth() + 1,
  day: _today.getDate()
};

export default {
  name: "Calendar",
  data() {
    return {
      hoverFrom: new Date(),
      hoverTo: new Date(),
      choosingNew: true,
      month: _todayComps.month,
      year: _todayComps.year
    };
  },
  computed: {
    from() {
      return this.getFilters.pickup_date;
    },
    to() {
      return this.getFilters.dropoff_date;
    },
    // Our component exposes month as 1-based, but sometimes we need 0-based
    monthIndex() {
      return this.month - 1;
    },
    isLeapYear() {
      return (
        (this.year % 4 === 0 && this.year % 100 !== 0) || this.year % 400 === 0
      );
    },
    // Day/month/year components for previous month
    previousMonthComps() {
      if (this.month === 1)
        return {
          days: _daysInMonths[11],
          month: 12,
          year: this.year - 1
        };
      return {
        days:
          this.month === 3 && this.isLeapYear
            ? 29
            : _daysInMonths[this.month - 2],
        month: this.month - 1,
        year: this.year
      };
    },
    // Day/month/year components for next month
    nextMonthComps() {
      if (this.month === 12)
        return {
          days: _daysInMonths[0],
          month: 1,
          year: this.year + 1
        };
      return {
        days:
          this.month === 2 && this.isLeapYear ? 29 : _daysInMonths[this.month],
        month: this.month + 1,
        year: this.year
      };
    },
    // State for calendar header (no dependencies yet...)
    months() {
      return _monthLabels.map((ml, i) => ({
        label: ml,
        label_1: ml.substring(0, 1),
        label_2: ml.substring(0, 2),
        label_3: ml.substring(0, 3),
        number: i + 1
      }));
    },
    // State for weekday header (no dependencies yet...)
    weekdays() {
      return _weekdayLabels.map((wl, i) => ({
        label: wl,
        label_1: wl.substring(0, 1),
        label_2: wl.substring(0, 2),
        label_3: wl.substring(0, 3),
        number: i + 1
      }));
    },
    // State for calendar header
    header() {
      const month = this.months[this.monthIndex];
      return {
        month: month,
        year: this.year.toString(),
        shortYear: this.year.toString().substring(2, 4),
        label: month.label + " " //+ this.year
      };
    },
    header2() {
      const month = this.months[this.nextMonthComps.month - 1];
      return {
        month: month,
        year: this.nextMonthComps.year.toString(),
        shortYear: this.nextMonthComps.year.toString().substring(2, 4),
        label: month.label + " " + this.nextMonthComps.year
      };
    },
    // Returns number for first weekday (1-7), starting from Monday
    firstWeekdayInMonth() {
      let day = new Date(this.year, this.monthIndex, 1).getDay();
      if (day === 0) day = 7;
      return day;
    },
    firstWeekdayInNextMonth() {
      let day = new Date(
        this.nextMonthComps.year,
        this.nextMonthComps.month - 1,
        1
      ).getDay();
      if (day === 0) day = 7;
      return day;
    },
    // Returns number of days in the current month
    daysInMonth() {
      // Check for February in a leap year
      if (this.month === 2 && this.isLeapYear) return 29;
      // ...Just a normal month
      return _daysInMonths[this.monthIndex];
    },
    weeks() {
      const weeks = [];
      let previousMonth = true,
        thisMonth = false,
        nextMonth = false;
      let day = this.previousMonthComps.days - this.firstWeekdayInMonth + 2;
      let month = this.previousMonthComps.month;
      let year = this.previousMonthComps.year;
      // Cycle through each week of the month, up to 6 total
      for (let w = 1; w <= 6 && !nextMonth; w++) {
        // Cycle through each weekday
        const week = [];
        for (let d = 1; d <= 7; d++) {
          // We need to know when to start counting actual month days
          if (previousMonth && d >= this.firstWeekdayInMonth) {
            // Reset day/month/year counters
            day = 1;
            month = this.month;
            year = this.year;
            // ...and flag we're tracking actual month days
            previousMonth = false;
            thisMonth = true;
          }

          // Append day info for the current week
          // Note: this might or might not be an actual month day
          //  We don't know how the UI wants to display various days,
          //  so we'll supply all the data we can
          const dayInfo = {
            label: day && thisMonth ? day.toString() : "",
            day,
            weekday: d,
            week: w,
            month,
            year,
            date: new Date(year, month - 1, day),
            beforeMonth: previousMonth,
            afterMonth: nextMonth,
            inMonth: thisMonth,
            isToday:
              day === _todayComps.day &&
              month === _todayComps.month &&
              year === _todayComps.year,
            isFirstDay: thisMonth && day === 1,
            isLastDay: thisMonth && day === this.daysInMonth
          };

          week.push(dayInfo);

          // We've hit the last day of the month
          if (thisMonth && day >= this.daysInMonth) {
            thisMonth = false;
            nextMonth = true;
            day = 1;
            month = this.nextMonthComps.month;
            year = this.nextMonthComps.year;
            // Still in the middle of the month (hasn't ended yet)
          } else {
            day++;
          }
        }
        // Append week info for the month
        weeks.push(week);
      }
      return weeks;
    },
    weeks2() {
      const weeks = [];
      let previousMonth = true,
        thisMonth = false,
        nextMonth = false;
      let day = this.daysInMonth - this.firstWeekdayInNextMonth + 2;
      let month = this.month;
      let year = this.year;
      // Cycle through each week of the month, up to 6 total
      for (let w = 1; w <= 6 && !nextMonth; w++) {
        // Cycle through each weekday
        const week = [];
        for (let d = 1; d <= 7; d++) {
          // We need to know when to start counting actual month days
          if (previousMonth && d >= this.firstWeekdayInNextMonth) {
            // Reset day/month/year counters
            day = 1;
            month = this.nextMonthComps.month;
            year = this.nextMonthComps.year;
            // ...and flag we're tracking actual month days
            previousMonth = false;
            thisMonth = true;
          }

          // Append day info for the current week
          // Note: this might or might not be an actual month day
          //  We don't know how the UI wants to display various days,
          //  so we'll supply all the data we can
          const dayInfo = {
            label: day && thisMonth ? day.toString() : "",
            day,
            weekday: d,
            week: w,
            month,
            year,
            date: new Date(year, month - 1, day),
            beforeMonth: previousMonth,
            afterMonth: nextMonth,
            inMonth: thisMonth,
            isToday:
              day === _todayComps.day &&
              month === _todayComps.month &&
              year === _todayComps.year,
            isFirstDay: thisMonth && day === 1,
            isLastDay: thisMonth && day === this.daysInMonth
          };

          week.push(dayInfo);

          // We've hit the last day of the month
          if (thisMonth && day >= this.nextMonthComps.days) {
            thisMonth = false;
            nextMonth = true;
            day = 1;
            // Still in the middle of the month (hasn't ended yet)
          } else {
            day++;
          }
        }
        // Append week info for the month
        weeks.push(week);
      }
      return weeks;
    }
  },
  methods: {
    see_cars() {
      this.hide();
      this.$router.push({ path: "/myrentacar-vue/", query: this.getQuery });
    },
    hide: function() {
      const popup = document.getElementById("calendar-outer");
      const setting_button = document.querySelector(".settings__setting-dates");
      popup.style.height = "0px";
      setting_button.classList.remove("settings__setting-selected");
      document.getElementById("settings__divider").style.display = "block";
    },
    show: function() {
      const popup = document.getElementById("calendar-outer");
      const setting_button = document.querySelector(".settings__setting-dates");
      popup.style.height = "385px";
      setting_button.classList.add("settings__setting-selected");
      document.getElementById("settings__divider").style.display = "none";
    },
    shortMonthStr: function(date) {
      return _monthLabels[date.getMonth()].substring(0, 3).toLowerCase() + ".";
    },
    dayString: function(num) {
      num = num % 100;

      if (num < 10 || num > 20) {
        if (num % 10 === 1) return "день";
        else if (num % 10 < 5) return "дня";
      }
      return "дней";
    },
    togglePopup: function() {
      const popup = document.getElementById("calendar-outer");
      if (popup) {
        if (popup.style.height !== "385px") this.show();
        else this.hide();
      }
    },
    isDisabled(day) {
      if (!day.inMonth) return false;
      let today = new Date();
      today.setHours(0, 0, 0, 0);
      return day.date.getTime() < today;
    },
    isSelected(day) {
      return (
        day.inMonth &&
        (this.getFilters.pickup_date.getTime() === day.date.getTime() ||
          this.getFilters.dropoff_date.getTime() === day.date.getTime())
      );
    },
    isBetween(day) {
      return (
        day.inMonth &&
        (day.date < this.getFilters.dropoff_date &&
          day.date > this.getFilters.pickup_date)
      );
    },
    isHighlighted(day) {
      if (this.isSelected(day) || this.isBetween(day)) return false;
      if (this.isDisabled(day)) return false;
      return (
        day.inMonth && (day.date <= this.hoverTo && day.date >= this.hoverFrom)
      );
    },
    selectDay(day) {
      if (!day.inMonth) return;
      if (this.isDisabled(day)) return;
      //console.log(day.date);
      let f = this.getFilters.pickup_date;
      let t = this.getFilters.dropoff_date;

      if (this.choosingNew) {
        f = t = this.hoverFrom = this.hoverTo = day.date;
        this.choosingNew = false;
        this.$store.commit("setFilters", {
          pickup_date: f,
          dropoff_date: t
        });
        //console.log(this.from);
      } else {
        if (day.date > f) t = day.date;
        else {
          t = f;
          f = day.date;
        }
        this.choosingNew = true;
        this.$store.commit("setFilters", {
          pickup_date: f,
          dropoff_date: t
        });
        this.$store.commit("getCarsCount");
      }
    },
    hoverDay(day) {
      if (!day.inMonth || this.choosingNew) return;
      if (day.date > this.getFilters.pickup_date) {
        this.hoverTo = day.date;
        this.hoverFrom = this.getFilters.pickup_date;
      } else {
        this.hoverFrom = day.date;
        this.hoverTo = this.getFilters.dropoff_date;
      }
    },
    moveNextMonth() {
      if (this.month < 12) {
        this.month++;
      } else {
        this.month = 1;
        this.year++;
      }
    },
    movePreviousMonth() {
      if (this.month > 1) {
        this.month--;
      } else {
        this.month = 12;
        this.year--;
      }
    }
  },
  mounted() {
    //hide filter popup when clicking outside it:
    document.addEventListener("click", e => {
      let popup = document.querySelector("#calendar-outer");
      if (e.target.closest("#calendar-outer") || e.target.closest(".calendar"))
        return;

      if (popup.style.height === "385px") this.hide();
    });
  },
  created() {
    let d = new Date(this.getFilters.pickup_date);
    d.setMonth(d.getMonth() + 1);
    this.month = d.getMonth();
    this.year = d.getFullYear();
  }
};
</script>

<style scoped lang="scss">
@import "./calendar.scss";
</style>
