// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import Vuex from "vuex";
import App from "./App";
import VueRouter from "vue-router";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    filters: {},
    finished: false,
    cars_count: 0
  },
  mutations: {
    setFilters(state, filters) {
      for (var k in filters) {
        Vue.set(state.filters, k, filters[k]); //using Vue.set to make sure it's updated correctly
      }
    },
    getCarsCount(state) {
      state.finished = false;
      let url =
        "http://beta.myrentacar.co/api/cars/count?key=car_to_rent&signature=72af0db4fabfb23b278cbf60bbb9907b&insurance[]=2";
      for (var k in state.filters) {
        if (k === "pickup_date" || k === "dropoff_date") {
          const y = state.filters[k].getFullYear();
          const m = state.filters[k].getMonth() + 1;
          const d = state.filters[k].getDate();
          url +=
            "&" +
            k +
            "=" +
            "" +
            y +
            "-" +
            (m < 10 ? "0" : "") +
            m +
            "-" +
            (d < 10 ? "0" : "") +
            d;
        } else url += "&" + k + "=" + state.filters[k];
      }
      //console.log("vuex: " + url);
      const proxy_url = "https://cors-anywhere.herokuapp.com/";
      fetch(proxy_url + url)
        .then(response => response.json())
        .then(json => {
          state.cars_count = json.count;
          state.finished = true;
        });
    }
  }
});

Vue.use(VueRouter);
Vue.config.productionTip = false;

Vue.mixin({
  methods: {
    automobilesString: function(num) {
      if (num % 10 === 0) return "автомобилей";

      num = num % 100;

      if (num < 10 || num > 20) {
        if (num % 10 === 1) return "автомобиль";
        else if (num % 10 < 5) return "автомобиля";
      }
      return "автомобилей";
    },
    formatDate(date) {
      // Date to YYYY-MM-DD
      const y = date.getFullYear();
      const m = date.getMonth() + 1;
      const d = date.getDate();
      return (
        "" + y + "-" + (m < 10 ? "0" : "") + m + "-" + (d < 10 ? "0" : "") + d
      );
    },
    extractDate(dateString) {
      // YYYY-MM-DD to Date

      const y = dateString.substring(0, 4);
      const m = dateString.substring(5, 7);
      const d = dateString.substring(8, 10);
      return new Date(y, m - 1, d);
    },
    addParameters(url, params) {
      for (var k in params) {
        if (k === "pickup_date" || k === "dropoff_date") {
          url += "&" + k + "=" + this.formatDate(params[k]);
        } else url += "&" + k + "=" + params[k];
      }
      return url;
    },
    daysBetween: function(firstDate, secondDate) {
      return Math.max(
        1,
        Math.round((secondDate - firstDate) / (1000 * 60 * 60 * 24))
      );
    }
  },
  computed: {
    getFilters() {
      return this.$store.state.filters;
    },
    days() {
      return this.daysBetween(
        this.getFilters.pickup_date,
        this.getFilters.dropoff_date
      );
    },
    getQuery() {
      let result = {};
      for (var k in this.$store.state.filters) {
        if (k === "pickup_date" || k === "dropoff_date") {
          result[k] = this.formatDate(this.$store.state.filters[k]);
        } else result[k] = this.$store.state.filters[k];
      }

      return result;
    }
  }
});

const router = new VueRouter({
  mode: "history",

  routes: [
    {      
      path: "*",
      // Specify the component to be rendered for this route
      component: App,
      // Inject  props based on route.query values (our query parameters!)
      props: route => ({
        pickup_city_id: route.query.pickup_city_id,
        dropoff_city_id: route.query.dropoff_city_id,
        pickup_date: route.query.pickup_date,
        dropoff_date: route.query.dropoff_date
      })
    }
  ]
});

/* eslint-disable no-new */
new Vue({
  router,
  el: "#app",
  components: { App },
  store,
  template: "<router-view><App/></router-view>"
});
