<template>
  <div class="city-picker">
    <a
      href="#"
      v-on:click.prevent="toggleCityPicker();"
      class="settings__setting settings__setting-city flex-column"
    >
      <span class="settings__label">Город получения и возврата</span>
      <span class="settings__choice"
        >{{ pickup_city_name
        }}<span v-if="getFilters.pickup_city_id !== getFilters.dropoff_city_id">
          – {{ dropoff_city_name }}</span
        ></span
      >
    </a>
    <div id="city-picker-outer">
      <div class="city-picker-inner">
        <h3>
          <a v-on:click.prevent="setMode(true);" href="#" id="pick-up"
            >Получение</a
          >
          и
          <a v-on:click.prevent="setMode(false);" href="#" id="dropoff"
            >возврат</a
          ><span
            v-if="getFilters.pickup_city_id === getFilters.dropoff_city_id"
            id="same-city"
            >Там же</span
          >
        </h3>
        <div id="city-picker__active-bar"></div>
        <div id="city-picker__cities">
          <a
            href="#"
            class="city-picker__city"
            v-on:click.prevent="selectCity(city.id, city.name);"
            v-for="city in cities"
            :key="city.key"
            v-bind:class="classObject(city.id)"
          >
            {{ city.name }}
            <svg
              v-if="city.airport"
              width="14"
              height="15"
              viewBox="0 0 14 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M14 10.5V9L8.10526 5.25V1.125C8.10526 0.5025 7.61158 0 7 0C6.38842 0 5.89474 0.5025 5.89474 1.125V5.25L0 9V10.5L5.89474 8.625V12.75L4.42105 13.875V15L7 14.25L9.57895 15V13.875L8.10526 12.75V8.625L14 10.5Z"
                v-bind:fill="
                  city.id ===
                  (choosing_pick_up_city
                    ? getFilters.pickup_city_id
                    : getFilters.dropoff_city_id)
                    ? '#27C79A'
                    : '#1E2A35'
                "
              />
            </svg>
          </a>
        </div>
        <div class="city-picker__buttons">
          <a
            href="#"
            v-if="$store.state.finished && $store.state.cars_count > 0"
            v-on:click.prevent="see_cars();"
            id="city-picker__show-cars"
            >Посмотреть {{ $store.state.cars_count }}
            {{ automobilesString($store.state.cars_count) }}</a
          >
          <span
            v-if="$store.state.finished && $store.state.cars_count == 0"
            class="city-picker__not-found"
            >Автомобили не найдены</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CityPicker",
  data() {
    return {
      cities: [],
      pickup_city_name: "",
      dropoff_city_name: "",
      choosing_pick_up_city: true,
      choosing_drop_off_city: true,
      filters: {}
    };
  },
  methods: {
    see_cars() {
      this.hide();
      this.$router.push({ path: "/myrentacar-vue/", query: this.getQuery });
    },
    hide: function() {
      const popup = document.getElementById("city-picker-outer");
      const setting_button = document.querySelector(".settings__setting-city");
      popup.style.maxHeight = "0px";
      setting_button.classList.remove("settings__setting-selected");
      document.getElementById("settings__divider").style.display = "block";
    },
    show: function() {
      const popup = document.getElementById("city-picker-outer");
      const setting_button = document.querySelector(".settings__setting-city");
      popup.style.maxHeight = "580px";
      setting_button.classList.add("settings__setting-selected");
      document.getElementById("settings__divider").style.display = "none";
    },
    toggleCityPicker: function() {
      const popup = document.getElementById("city-picker-outer");
      if (popup) {
        if (popup.style.maxHeight !== "580px") this.show();
        else this.hide();
      }
    },
    setMode: function(mode) {
      this.choosing_pick_up_city = mode;
      this.choosing_drop_off_city = !mode;
      //move the underline:
      const bar = document.getElementById("city-picker__active-bar");
      let target = this.choosing_pick_up_city
        ? document.getElementById("pick-up")
        : document.getElementById("dropoff");

      bar.style.left = target.offsetLeft + "px";
      bar.style.width = target.clientWidth + "px";
    },
    getCities: function() {
      const url = "http://beta.myrentacar.co/json/124/general";
      const proxy_url = "https://cors-anywhere.herokuapp.com/";
      fetch(proxy_url + url)
        .then(response => response.json())
        .then(json => {
          json.city.cities.small.forEach(column => {
            column.forEach(element => {
              this.cities.push(element);
              if (element.id === this.getFilters.pickup_city_id) {
                this.pickup_city_name = element.name;
              }
              if (element.id === this.getFilters.dropoff_city_id) {
                this.dropoff_city_name = element.name;
              }
            });
          });
        });
    },

    selectCity: function(id, name) {
      if (this.choosing_pick_up_city) {
        this.$store.commit("setFilters", { pickup_city_id: id });
        this.pickup_city_name = name;
      }

      if (this.choosing_drop_off_city) {
        this.$store.commit("setFilters", { dropoff_city_id: id });
        this.dropoff_city_name = name;
      }

      this.$store.commit("getCarsCount");
    },
    classObject(id) {
      if (this.choosing_pick_up_city) {
        if (id === this.getFilters.pickup_city_id) return "selected";
      } else {
        if (id === this.getFilters.dropoff_city_id) return "selected";
      }
    }
  },
  mounted() {
    //hide filter popup when clicking outside it:
    document.addEventListener("click", e => {
      let popup = document.querySelector("#city-picker-outer");
      if (
        e.target.closest("#city-picker-outer") ||
        e.target.closest(".city-picker")
      )
        return;

      if (popup.style.maxHeight === "580px") this.hide();
    });
  },
  created() {
    this.getCities();
  }
};
</script>

<style scoped lang="scss">
@import "./citypicker.scss";
</style>
